import React from "react"
import { graphql, Link } from "gatsby"

import SEO from "../components/seo"

import {
  Icon,
  Container,
  Section,
  Main,
  Header,
  Footer,
} from "../components/Lib"
import { BlogPosts } from "../pages/blog"
import { toPersianNumber } from "../utils/helpers"

const Pagination = ({ currentPage, numberOfPages }) => {
  return (
    <div className="pagination">
      {currentPage + 1 <= numberOfPages && (
        <Link
          to={`/blog/all/${currentPage + 1}`}
          className="pagination__icon navigator"
        >
          <Icon name="arrow_right" />
        </Link>
      )}
      <div className="pagination__current">
        صفحه {toPersianNumber(currentPage)} از {toPersianNumber(numberOfPages)}
      </div>
      {currentPage - 1 > 0 && (
        <Link
          to={`/blog/all/${currentPage - 1}`}
          className="pagination__icon navigator"
        >
          <Icon name="arrow_left" />
        </Link>
      )}
    </div>
  )
}

const all = ({ data, pageContext }) => {
  const posts = data.posts.edges
  return (
    <Container>
      <SEO title="همه نوشته ها" description="نوشته های بلاگ بایوکست در یک جا" />
      <Header />
      <Main>
        <Section className="all_posts">
          <h1 className="all_posts__title">همه نوشته‌ها</h1>
          <div className="inner_wrapper">
            <BlogPosts posts={posts} />
            <Pagination
              currentPage={pageContext.currentPage}
              numberOfPages={pageContext.numberOfPages}
            />
          </div>
        </Section>
      </Main>
      <Footer beforeColor="white" />
    </Container>
  )
}

export const query = graphql`
  query($skip: Int!, $limit: Int!) {
    posts: allStrapiPost(
      sort: { fields: created_at, order: DESC }
      limit: $limit
      skip: $skip
    ) {
      edges {
        node {
          title
          created_at
          description
          content
          author {
            firstName
            lastName
          }
          mainImage {
            childImageSharp {
              fluid(quality: 70) {
                src
              }
            }
          }
          category {
            name
          }
          fields {
            readingTime
            slug
          }
        }
      }
    }
  }
`

export default all
